.border_st span {
  position: absolute;
  width: 25px;
  height: 25px;
}
.border_st span::before {
  content: "";
  position: absolute;
  width: 1px;
  height: 70%;
  background-color: #000;
}
.border_st span::after {
  content: "";
  position: absolute;
  width: 70%;
  height: 1px;
  background-color: #000;
}
.border_st .top_left {
  left: -1.3rem;
  top: -1.3rem;
}
.border_st .top_left::before {
  top: 0;
  right: 0;
}
.border_st .top_left::after {
  bottom: 0;
  left: 0;
}
.border_st .top_right {
  right: -1.3rem;
  top: -1.3rem;
}
.border_st .top_right::before {
  top: 0;
  left: 0;
}
.border_st .top_right::after {
  bottom: 0;
  right: 0;
}
.border_st .bottom_left {
  left: -1.3rem;
  bottom: -1.3rem;
}
.border_st .bottom_left::before {
  bottom: 0;
  right: 0;
}
.border_st .bottom_left::after {
  top: 0;
  left: 0;
}
.border_st .bottom_right {
  right: -1.3rem;
  bottom: -1.3rem;
}
.border_st .bottom_right::before {
  bottom: 0;
  left: 0;
}
.border_st .bottom_right::after {
  top: 0;
  right: 0;
}

@media (max-width: 1200px) {
  .border_st span {
    width: 20px;
    height: 20px;
  }
  .border_st .top_left {
    left: -1.3rem;
    top: -1.3rem;
  }
  .border_st .top_right {
    right: -1.3rem;
    top: -1.3rem;
  }
  .border_st .bottom_left {
    left: -1.3rem;
    bottom: -1.3rem;
  }
  .border_st .bottom_right {
    right: -1.3rem;
    bottom: -1.3rem;
  }
}
@media (max-width: 500px) {
  .border_st span {
    width: 15px;
    height: 15px;
  }
  .border_st .top_left {
    left: -1rem;
    top: -1rem;
  }
  .border_st .top_right {
    right: -1rem;
    top: -1rem;
  }
  .border_st .bottom_left {
    left: -1rem;
    bottom: -1rem;
  }
  .border_st .bottom_right {
    right: -1rem;
    bottom: -1rem;
  }
}
.scroll_btn {
