}
header .navbar-brand {
  padding: 0;
}
header .navbar-brand img {
  width: 40px;
}
header .nav_toggle_btn {
  border: 0;
  width: 25px;
  cursor: pointer;
  background-color: transparent;
}
header .nav_toggle_btn span {
  width: 100%;
  height: 1px;
  border-radius: 1px;
  background-color: #fff;
  display: block;
  margin: 0.4rem 0;
}
header .nav_toggle_btn:focus-visible, header .nav_toggle_btn:focus {
  outline: none;
  border: none;
  box-shadow: none;
}
header .wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 1.5rem;
}
header .wrapper .socials {
  list-style: none;
  display: flex;
  justify-content: flex-end;
  column-gap: 1.15rem;
}
header .wrapper .socials li a {
  color: #fff;
  font-size: 15px;
}
header .wrapper .socials li a svg {
  font-size: 1.5rem;
}
header .wrapper .socials li a path {
  color: #fff;
}
header .side_navigation {
  z-index: 99999;
  position: fixed;
  background-color: #000;
  top: 0;
  right: 0;
  min-width: 43%;
  height: 100%;
  padding: 7rem 2rem 1rem;
  transform: translateX(1600px);
  transition: 1s all ease-in-out;
  overflow-y: scroll;
  scrollbar-width: none;
}
header .side_navigation::-webkit-scrollbar {
  width: 0;
}
header .side_navigation .socials {
  margin-top: 3rem;
}
header .side_navigation .menu_ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  row-gap: 1.25rem;
}
header .side_navigation .menu_ul li {
  transform: translateX(900px);
  transition: 0.4s all ease-in-out;
}
header .side_navigation .menu_ul li .nav_link {
  display: flex;
  justify-content: flex-start;
  column-gap: 0.5rem;
  align-items: center;
  color: #fff;
  text-transform: uppercase;
  font-family: QuicksandRegular;
  opacity: 0.8;
  font-size: clamp(12px, 1.5vw, 15px);
  transition: 0.2s all ease-in-out;
}
header .side_navigation .menu_ul li .nav_link .common_icon_st {
  margin: 0;
}
header .side_navigation .menu_ul li .nav_link.active {
  opacity: 1;
}
header .side_navigation .menu_ul li .nav_link:hover {
  opacity: 1;
}
header .side_navigation.active {
  transform: translateX(0);
}
header .side_navigation.active .menu_ul li {
  transform: translateX(0);
}
header .side_navigation .close_btn {
  position: fixed;
  right: 1rem;
  top: 2rem;
  font-size: 5rem;
  color: #fff;
  line-height: 0;
  font-weight: 100;
  cursor: pointer;
  z-index: 2;
}
header .overlay_shadow {
  position: fixed;
  height: 100vh;
  width: 100%;
  top: 0;
  transform: translateX(3000px);
  background-color: rgba(0, 0, 0, 0.526);
  display: block;
  z-index: 9999;
  transition: 0.8s all ease-in-out;
}
header .overlay_shadow.active {
  transform: translateX(0);
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
header .side_navigation li:nth-child(1) {
  transition-delay: 0.1s;
}

header .side_navigation li:nth-child(2) {
  transition-delay: 0.2s;
}

header .side_navigation li:nth-child(3) {
  transition-delay: 0.3s;
}

header .side_navigation li:nth-child(4) {
  transition-delay: 0.4s;
}

header .side_navigation li:nth-child(5) {
  transition-delay: 0.5s;
}

header .side_navigation li:nth-child(6) {
  transition-delay: 0.6s;
}

header .side_navigation li:nth-child(7) {
  transition-delay: 0.7s;
}

header .side_navigation li:nth-child(8) {
  transition-delay: 0.8s;
}

header .side_navigation li:nth-child(9) {
  transition-delay: 0.9s;
}

header .side_navigation li:nth-child(10) {
  transition-delay: 1s;
}

@media (max-width: 1080px) {
  header .side_navigation {
    padding: 5rem 1rem 1rem;
  }

  header .side_navigation .menu_ul li .nav_link {
    font-size: 0.85rem;
